<template>
  <div class="mt-4">
    <v-card-title>
      <div class="text-h5 font-weight-light">
        Organization Chart
      </div>
      <v-spacer></v-spacer>
      <v-btn color="primary" @click="create()" depressed elevation="2">
        <v-icon>mdi-plus</v-icon>
        Tambah</v-btn>
    </v-card-title>
    <v-card class="pa-2 ma-2">
      <v-card-title>
        <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details
          width="100px"></v-text-field>
      </v-card-title>
      <v-data-table class="elevation-1" :headers="headers" :items="data.data" loading-text="Loading... Please wait" :loading="data.loading"
        :search="search">
        <template #[`item.index`]="{ item }">
          {{ data.data.indexOf(item) + 1 }}.
        </template>
        <template #[`item.isactive`]="{ item }">
            {{ item.isactive ? 'Active' : 'Non Active' }}
        </template>
        <template #[`item.action`]="{ item }">
          <v-btn icon color="success" @click="detail(item)">
            <v-icon small>
              mdi-eye
            </v-icon>
          </v-btn>
          <v-btn icon color="warning" @click="edit(item)">
            <v-icon small>
              mdi-lead-pencil
            </v-icon>
          </v-btn>
          <v-btn icon color="red" @click="hapus(item.id)">
            <v-icon small>
              mdi-delete
            </v-icon>
          </v-btn>
        </template>
      </v-data-table>
    </v-card>
    <Create/>
    <Update/>
    <Detail/>
  </div>
</template>
<script>
import { mapState, mapActions } from 'vuex'
import store from '@/store'
import Create from './../components/Organization/Create.vue'
import Update from './../components/Organization/Update.vue'
import Detail from './../components/Organization/Detail.vue'

export default {
  name: 'OrganizationPage',
  components: {
    Create, Update, Detail
  },
  data: () => ({
    search: '',
    headers: [
      {
        text: 'OchartID',
        align: 'start',
        sortable: false,
        value: 'OchartID'
      },
      { text: 'TitleName', value: 'TitleName' },
      { text: 'PIC', value: 'PIC' },
      { text: 'Quotes', value: 'Quotes' },
      { text: 'Status', value: 'isactive' },
      { text: 'Action', value: 'action' }
    ]
  }),
  mounted: function () {
    store.dispatch('organization/reqData')
  },
  computed: {
    ...mapState('organization', {
      data: (state) => state
    })
  },
  methods: {
    ...mapActions('organization', [
      'edit', 'detail', 'hapus', 'create'
    ])
  }
}
</script>
