<template>
  <v-row justify="center">
    <v-dialog v-model="state.dialogDetail" persistent fullscreen scrollable hide-overlay
      transition="dialog-bottom-transition">
      <v-card tile>
        <v-toolbar fixed dark color="primary">
          <v-btn icon dark @click="state.dialogCreate = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title>Detail</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn color="yellow" text :loading="state.loading" @click="edit(state.detail)">
              EDIT
            </v-btn>
            <v-btn color="white" text :loading="state.loading" @click="state.dialogDetail = false">
              CLOSE
            </v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <v-card-text>
          <v-container>
            <v-row dense class="mt-10">
              <v-col cols="12" md="12">
                <center><v-img readonly :src="state.detail.ImagesPath" max-width="300px"></v-img></center>
              </v-col>
              <v-col cols="12" md="12" mb="0">
                <v-text-field label="PIC" readonly v-model="state.detail.PIC" outlined></v-text-field>
              </v-col>
              <v-col cols="12" md="12">
                <v-text-field label="Jabatan" readonly v-model="state.detail.TitleName" outlined></v-text-field>
              </v-col>
              <v-col cols="12" md="12">
                <v-text-field label="Keyword" readonly v-model="state.detail.Quotes" outlined></v-text-field>
              </v-col>
              <v-col cols="12" md="12">
                <v-select v-model="state.detail.isactive" readonly :items="items" item-text="text" item-value="value" label="Status"
                  outlined></v-select>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>

        <div style="flex: 1 1 auto;"></div>
      </v-card>
    </v-dialog>

  </v-row>
</template>
<script>
import { mapState, mapActions } from 'vuex'
export default {
  name: 'OrganizationCreate',
  data () {
    return {
      form: {
        ImagesPath: ''
      },
      items: [
        {
          text: 'Active', value: '1'
        },
        {
          text: 'No Active', value: '0'
        }
      ]
    }
  },
  inject: {
    theme: {
      default: { isDark: false }
    }
  },
  computed: {
    ...mapState('organization', {
      state: (state) => state
    })
  },
  methods: {
    ...mapActions('organization', [
      'edit'
    ]),
    createBase64Image: function (FileObject) {
      const reader = new FileReader()
      reader.onload = (event) => {
        this.form.ImagesPath = event.target.result
      }
      reader.readAsDataURL(FileObject)
    }
  }
}
</script>
