<template>
  <v-row justify="center">
    <v-dialog v-model="state.dialogEdit" persistent fullscreen scrollable hide-overlay
      transition="dialog-bottom-transition">
      <v-card tile>
        <v-toolbar fixed dark color="primary">
          <v-btn icon dark @click="state.dialogEdit = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title>Update Ochart</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn color="yellow" text :loading="state.loading" @click="update(state.edit)">
              UPDATE
            </v-btn>
            <v-btn color="white" text :loading="state.loading" @click="state.dialogEdit = false">
              CLOSE
            </v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <v-card-text>
          <v-container>
            <v-row dense class="mt-10">
              <v-col cols="12" md="12" mb="0">
                <v-text-field label="PIC" v-model="state.edit.PIC" outlined></v-text-field>
              </v-col>
              <v-col cols="12" md="12">
                <v-text-field label="Jabatan" v-model="state.edit.TitleName" outlined></v-text-field>
              </v-col>
              <v-col cols="12" md="12">
                <v-text-field label="Keyword" v-model="state.edit.Quotes" outlined></v-text-field>
              </v-col>
              <v-col cols="12" md="12">
                <v-img class="pl-20" :src="state.edit.ImagesPath" max-width="100px"></v-img>
                <v-file-input @change="createBase64Image" color="deep-purple accent-4" counter accept="image/*"
                  label="Upload Image" placeholder="Select your files" prepend-icon="mdi-paperclip" outlined
                  :show-size="1000">
                  <template v-slot:selection="{ index, text }">
                    <v-chip v-if="index < 2" color="deep-purple accent-4" dark label small>
                      {{ text }}
                    </v-chip>

                    <span v-else-if="index === 2" class="text-overline grey--text text--darken-3 mx-2">
                      +{{ files.length - 2 }} File(s)
                    </span>
                  </template>
                </v-file-input>
              </v-col>
              <v-col cols="12" md="12">
                <v-select v-model="state.edit.isactive" :items="items" item-text="text" item-value="value" label="Status"
                  outlined></v-select>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>

        <div style="flex: 1 1 auto;"></div>
      </v-card>
    </v-dialog>

  </v-row>
</template>
<script>
import { mapState, mapActions } from 'vuex'
export default {
  name: 'OrganizationCreate',
  data () {
    return {
      form: {
        ImagesPath: ''
      },
      items: [
        {
          text: 'Active', value: '1'
        },
        {
          text: 'No Active', value: '0'
        }
      ]
    }
  },
  inject: {
    theme: {
      default: { isDark: false }
    }
  },
  computed: {
    ...mapState('organization', {
      state: (state) => state
    })
  },
  methods: {
    ...mapActions('organization', [
      'update'
    ]),
    createBase64Image: function (FileObject) {
      const reader = new FileReader()
      reader.onload = (event) => {
        this.state.edit.ImagesPath = event.target.result
      }
      reader.readAsDataURL(FileObject)
    }
  }
}
</script>
